.App {
  width: 80%;
  margin: 0 auto;
}
.section {
  margin: 2rem 0 0 0;
  border: 1px solid #ccc;
  border-radius: 2rem;
  position: relative;
  text-align: center;
}
.sectionHeader {
  padding: 0 0.5rem;
  position: absolute;
  top: -1rem;
  left: 10%;
  background: #fff;
}



.searchInputs {
  display: flex;
  justify-content: space-around;
}
.searchInput {
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
}
.searchInput > label {
  margin: 0 0 0.5rem 0;
}
.searchButton {
  height: 3rem;
  width: 10rem;
  margin: 0 .5rem 2rem .5rem;
  background: none;
  border: 1px solid #ccc;
  border-radius: 2rem;
  transition: all .5s ease;
}
.searchButton:hover{
  background: #eee;
  border: 1px solid #aaa;
  transform: scale(1.1);
}


.listInnerWrapper {
  padding: 2.5rem;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-auto-rows: auto;
  grid-gap: 2rem;
}
.item {
  border: 1px solid #ccc;
  border-radius: 2rem;
  transition: all .5s ease;
}
.item:hover,
.paginationItem:hover {
  background: #eee;
  transform: scale(1.1);
}
.itemInnerWrapper {
  padding: 3rem 0 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.itemInfoWrapper {
  display: flex;
  width: 10rem;
  flex-direction: column;
}
.itemInfoWrapper > div {
  margin: .25rem 0;
}
.itemImgWrapper {
  height: 10rem;
  width: 10rem;
  margin: 0 0 1rem 0;
  position: relative;
}
.avatar {
  max-width: 100%;
  max-height: 100%;
  border-radius: 2rem;
}
.favoriteMark {
  width: 4rem;
  height: 4rem;
  position: absolute;
  top: -2.25rem;
  right: -2rem;
}



.filters, .pagination {
  margin-left: 2.5rem;
  margin-right: 2.5rem;
  padding: 0 3rem 0 3rem;
  display: flex;
}
.filters > .sectionHeader,
.pagination > .sectionHeader {
  top: -0.75rem;
}
.filterItem {
  padding: 0.5rem;
  margin: 1.2rem 0.5rem;
}


.pagination {
  padding-top: .6rem;
  padding-bottom: .6rem;
  flex-wrap: wrap;
}
.paginationItem {
  padding: 0.5rem;
  margin: .3rem 0.5rem;
  border: 1px solid #ccc;
  background: #fff;
  border-radius: 0.3rem;
  transition: all .5s ease;
  cursor: pointer;
}
.paginationItem:disabled {
  background: #ccc;
  border: 1px solid #aaa;
  pointer-events:none;
}



.toMainButton {
  display: inline-flex;
  color: #000;
  text-decoration: none;
  align-items: center;
  justify-content: center;
  margin: 2rem auto;
}
.favoritesButton{
  display: inline-flex;
  margin: 0 .5rem 2rem .5rem;
  color: #000;
  text-decoration: none;
  align-items: center;
  justify-content: center;
}


@media only screen and (min-width: 750px) {
	.listInnerWrapper {
    grid-template-columns: repeat(2, 1fr);
	}
}
@media only screen and (min-width: 1050px) {
	.listInnerWrapper {
    grid-template-columns: repeat(3, 1fr);
	}
}
@media only screen and (min-width: 1366px) {
	.listInnerWrapper {
    grid-template-columns: repeat(4, 1fr);
	}
}
@media screen and (max-width: 770px) {
  .searchInputs {
    flex-direction: column;
  }
  .searchInput {
    padding: 1.5rem 3rem;
  }
  .filters {
    flex-direction: column;
    padding: 1rem;
  }
  .filterItem {
    padding: 0;
  }
}